.movies-list{
    display: flex;
    flex-direction: column;
    flex-flow: wrap;
    justify-content: center;
    margin-top: 70px;
}

#loader{
    position: absolute;
    top: 50%;
    left: 50%;
    margin-left: -57px;
    margin-top: -50px;
}

@media screen and (max-width: 380px){
    #loader{
        margin-left: -40px;
    }
}