body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#footer{
    background-color: rgba(21, 212, 123, 0.685);
    color: white;
    position: fixed;
    width: 100%;
    bottom: 0%;
    height: 1.2rem;
    text-align: center;
    font-size: 15px;

}
@-webkit-keyframes fadein {
    from {
        opacity:0;
    }
    to {
        opacity:1;
    }
}

@keyframes fadein {
    from {
        opacity:0;
    }
    to {
        opacity:1;
    }
}

@-webkit-keyframes half-fadein {
    from {
        opacity:0.7;
    }
    to {
        opacity:1;
    }
}

@keyframes half-fadein {
    from {
        opacity:0.7;
    }
    to {
        opacity:1;
    }
}
.movie-card{
    overflow: visible;
    display: inline-block;
    border-radius: 0px;
    margin-left: 20px;
    margin-right: 20px;
    margin-top: 20px;
    margin-bottom: 20px;
    height: 278px;
    width: 185px;
    transition: 1s ease-in-out;
    overflow: visible;
    -webkit-animation: fadein 1s;
            animation: fadein 1s;


    box-shadow: 5px 6px 3px 0px rgba(0, 0, 0, 0.8),
                0px 1px 1px 0px rgba(0, 0, 0, 0.84),
                0px 2px 1px -1px rgba(0, 0, 0, 0.82);
}


.movie-card-info{
    height:278px;
    width:185px;
    visibility: hidden;
    background: transparent;
    color:white;
}

.movie-card-info-wrapper:hover .movie-card-info{
    visibility: visible;
    background-image: linear-gradient(rgba(0, 0, 0, 0.829),rgba(102, 100, 100, 0.65));
    -webkit-animation: fadein 0.15s;
            animation: fadein 0.15s;
}

.movie-title{
    color:rgb(5, 196, 107);
    display: inline;
}

.movie-year{
    display: inline;
}

.movie-header{
    min-height:3rem;
}

.movie-toolbar{
    float: right;
    display: flex;
    margin-right:-13px;
}

.edit-btn,
.delete-btn{
    border: 3px solid transparent;
    border-radius: 100%;
    padding: 3px;
    cursor: pointer;
}

.edit-btn:hover,
.delete-btn:hover{
    background-color: rgba(12, 161, 62, 0.404);
    -webkit-animation: half-fadein 1s;
            animation: half-fadein 1s;
}

.movies-list{
    display: flex;
    flex-direction: column;
    flex-flow: wrap;
    justify-content: center;
    margin-top: 70px;
}

#loader{
    position: absolute;
    top: 50%;
    left: 50%;
    margin-left: -57px;
    margin-top: -50px;
}

@media screen and (max-width: 380px){
    #loader{
        margin-left: -40px;
    }
}
@-webkit-keyframes border-animation{
    from{
        border-color: rgba(0,0,0,0);

    }

    to{
        border-color: rgba(5, 196, 107, 1);
    }
}

@keyframes border-animation{
    from{
        border-color: rgba(0,0,0,0);

    }

    to{
        border-color: rgba(5, 196, 107, 1);
    }
}

#add-form{
    max-width:300px;
    /* font-family: 'Roboto'; */
}

.form-text-field{
    width:100%;
    outline: 0;
    border-width: 0 0 2px;
    /* font-family: 'Roboto'; */
}

.form-text-field:focus{
    -webkit-animation: border-animation 0.5s;
            animation: border-animation 0.5s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
}

.form-text-field:hover::-webkit-input-placeholder{
    color:rgba(5, 196, 107, 1);
    font-weight: bold;
}

.form-text-field:hover::-ms-input-placeholder{
    color:rgba(5, 196, 107, 1);
    font-weight: bold;
}

.form-text-field:hover::placeholder{
    color:rgba(5, 196, 107, 1);
    font-weight: bold;
}

.dialog-actions{
    margin-top: 10px;
    display:flex;
    justify-content: center;
    
}

.dialog-btn{
    background-color:seagreen;
    color: white;
    padding: 8px 16px;
    font-size: 0.875rem;
    min-width: 64px;
    border: transparent;
    border-radius: 4px;
    margin: 10px 10px;

}

.dialog-btn:hover{
    background-color:rgb(39, 114, 71);
    cursor: pointer;
}

.dialog-btn-cancel{
    background-color: rgb(94, 92, 92)
}

.dialog-btn-cancel:hover{
    background-color: rgb(75, 74, 74)
}

.dialog-btn:disabled{
    background-color: rgba(0,0,0,0.3);
    cursor: unset;
}

.dialog-err{
    font-size: 14px;
    color: red;
    font-weight: bold;
}
@-webkit-keyframes top-to-bottom{
    from {
        background-image: linear-gradient(to bottom, rgba(0,0,0,1), rgba(0,0,0,0));
    }

    to {
        background-color: rgba(0, 0, 0, 0.575);
    }
}

@keyframes top-to-bottom{
    from {
        background-image: linear-gradient(to bottom, rgba(0,0,0,1), rgba(0,0,0,0));
    }

    to {
        background-color: rgba(0, 0, 0, 0.575);
    }
}

@-webkit-keyframes bottom-to-top{
    0% {
        background-color: rgba(0, 0, 0, 0.575);
    }

    50% {
        background-image: linear-gradient(to bottom, rgb(0, 0, 0), rgba(0,0,0,0));
    }

    100% {
        background-image: linear-gradient(to bottom, rgba(0,0,0,1), rgba(0,0,0,0));
    }
}

@keyframes bottom-to-top{
    0% {
        background-color: rgba(0, 0, 0, 0.575);
    }

    50% {
        background-image: linear-gradient(to bottom, rgb(0, 0, 0), rgba(0,0,0,0));
    }

    100% {
        background-image: linear-gradient(to bottom, rgba(0,0,0,1), rgba(0,0,0,0));
    }
}

.navbar{
    top: 0%;
    width: 100%;
    position: fixed;
    display: block;
    text-align: center;
    z-index: 100; /* Less than modal's */
}

.navbar-top{
    -webkit-animation: bottom-to-top 0.5s;
            animation: bottom-to-top 0.5s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
}

.navbar-bottom{
    -webkit-animation: top-to-bottom 0.5s;
            animation: top-to-bottom 0.5s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;

    
}

#add-btn-wrapper{
    float:right;
    margin-top: 38px;
    margin-right: 50px;
}



#add-btn{
    background-color: seagreen;
    color: white;
    padding: 8px;
    min-width: 36px;
    font-size: 0.875rem;
    border: transparent;
    border-radius: 4px;
}

#add-btn:hover{
    cursor: pointer;
    background-color: rgb(61, 114, 85);

}


@media screen and (max-width: 385px){
    #add-btn-wrapper{

        position: fixed;
        bottom: 10%;
        right: -15%;
    }
}





